import React, { useMemo } from 'react';
import { Grid, useTheme } from '@mui/material';
import Layout from 'components/Layout';
import PostCard from 'components/cards/PostCard';
import Paginator from 'components/Paginator';
import RowList from 'components/RowList';
import { Link, PageProps } from 'gatsby';
import { useRecoilState } from 'recoil';
import { languageAtom } from 'atoms/language';
import Jumbotron from './Jumbotron';

const HomePage = ({ data }: PageProps<Queries.PostsPageQuery>) => {
  const theme = useTheme();

  const {
    allMarkdownRemark: { edges, pageInfo },
  } = data;
  const [lang] = useRecoilState(languageAtom);

  const posts = useMemo(() => edges.map(p => p.node), [edges]);

  return (
    <Layout title={<Jumbotron />}>
      <RowList>
        <>
          {posts.map(p => {
            const path = p.fields?.path ?? '/';
            const title = p.frontmatter?.title ?? '';
            const excerpt = p.excerpt ?? '';
            const thumbnail = p.frontmatter?.thumbnail?.childImageSharp?.gatsbyImageData;
            const date = p.frontmatter?.date ?? '1991-01-01';

            return (
              <Grid
                item
                xs={12}
                key={p.frontmatter?.title}
                sx={{
                  transition: 'transform 0.2s ease-in-out',
                  '& a': {
                    textDecoration: 'none',
                  },
                  '&:hover': {
                    transform: 'scale(1.01)',
                  },
                  '& + &': {
                    borderTop: `1px solid ${theme.palette.divider}`,
                  },
                }}
              >
                <Link to={path}>
                  <PostCard title={title} description={excerpt} thumbnail={thumbnail} date={date} />
                </Link>
              </Grid>
            );
          })}
        </>
      </RowList>
      <Paginator pageCount={pageInfo.pageCount} currentPage={pageInfo.currentPage} defaultUrl="/" lang={lang} />
    </Layout>
  );
};

export default HomePage;
