import React, { ReactNode, useMemo } from 'react';
import { Grid, Typography, Box, useTheme, styled } from '@mui/material';
import format from 'date-fns/format';
import { GatsbyImage, IGatsbyImageData } from 'gatsby-plugin-image';

interface Props {
  title: string;
  thumbnail?: IGatsbyImageData;
  description: string;
  date?: string;
  badge?: ReactNode;
  onClick?: () => void;
}
const PostCard = ({ title, thumbnail, description, date, badge, onClick }: Props) => {
  const { spacing, breakpoints, typography, palette } = useTheme();

  const formattedDate = useMemo(() => {
    if (date == null) {
      return undefined;
    }

    return format(new Date(date), 'yyy MMM dd');
  }, [date]);

  return (
    <Box
      onClick={onClick}
      sx={{
        padding: spacing(2.5),
        '& *': {
          color: palette.text.primary,
        },
      }}
    >
      <Grid container spacing={2} alignItems="center">
        <Grid
          item
          xs={12}
          md
          sx={{
            order: 2,
            [breakpoints.up('md')]: {
              order: 1,
            },
          }}
        >
          <Box>
            {badge != null ? <Box sx={{ marginBottom: spacing(1) }}>{badge}</Box> : null}
            <Typography
              gutterBottom
              variant="body1"
              component="h2"
              fontFamily={`"Nanum Myeongjo", serif`}
              fontWeight={typography.fontWeightRegular}
            >
              {title}
            </Typography>
            <Typography
              gutterBottom
              component="p"
              variant="caption"
              fontSize={typography.caption.fontSize}
              color={palette.text.secondary}
              whiteSpace="pre-line"
            >
              {description}
            </Typography>
            <Typography
              variant="caption"
              component="p"
              fontFamily={`"Birthstone", serif`}
              fontWeight={200}
              fontSize={typography.body2.fontSize}
              color={palette.text.disabled}
              sx={{ marginTop: spacing(1) }}
            >
              {formattedDate}
            </Typography>
          </Box>
        </Grid>
        <Grid
          item
          xs={12}
          md="auto"
          sx={{
            order: 1,
            [breakpoints.up('md')]: {
              order: 2,
            },
          }}
        >
          <Box
            sx={{
              position: 'relative',
              width: '100%',
              paddingBottom: '70%',
              [breakpoints.up('md')]: {
                width: 120,
                paddingBottom: '100%',
              },
            }}
          >
            {thumbnail && <ThumbnailImage image={thumbnail} alt={title} />}
          </Box>
        </Grid>
      </Grid>
    </Box>
  );
};

const ThumbnailImage = styled(GatsbyImage)(({ theme }) => ({
  position: 'absolute',
  objectFit: 'cover',
  width: '100%',
  height: '100%',
  borderRadius: theme.shape.borderRadius,
}));

export default PostCard;
